<template>
	<div class="call">
		<el-form label-width="80px">
			<el-form-item>
				<el-upload class="upload-demo" drag :on-change="readJson" :auto-upload="false" action="alert" :limit=1>
					<i class="el-icon-upload"></i>
					<div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
					<div class="el-upload__tip" slot="tip">请上传合约的json文件</div>
				</el-upload>
			</el-form-item>
		</el-form>

		<el-select v-model="selector" placeholder="selector" @change="change">
			<el-option
			v-for="item in functions"
			:key="item.value"
			:label="item.label"
			:value="item.value">
			</el-option>
		</el-select>
		<el-input
			type="textarea"
			:autosize="{ minRows: 4, maxRows: 8}"
			v-model="funcInfo"
			:disabled="true">
		</el-input>
		<div id="workflow">
			<div class="box">
				<el-button type="primary" @click="encode">encode</el-button>
				<el-input
					type="textarea"
					:autosize="{ minRows: 4, maxRows: 8}"
					v-model="encodeParams">
				</el-input>
				<el-input
					type="textarea"
					:autosize="{ minRows: 4, maxRows: 8}"
					v-model="encodeResult"
					:disabled="true">
				</el-input>
			</div>
			
			<div class="box">
				<el-button type="primary" @click="decode">decode</el-button>
				<el-input
					type="textarea"
					:autosize="{ minRows: 4, maxRows: 8}"
					v-model="decodeParams">
				</el-input>
				<el-input
					type="textarea"
					:autosize="{ minRows: 4, maxRows: 8}"
					v-model="decodeResult"
					:disabled="true">
				</el-input>
			</div>
		</div>

	</div>
</template>

<script>

const ethers = require('ethers');

export default {
	name: 'Explain',
	data() {
		return {
			provider: null,
			signer: null,
			contractAbi: null,
			functions: [{
				value: '',
				label: ''
			}],
			selector: '',
			selectorIndex: '',
			funcInfo: '',
			encodeParams: '',
			encodeResult: '',
			decodeParams: '',
			decodeResult: ''
		}
	},
	mounted: function () {
		this.initMetaMask();
	},
	methods: {
		async initMetaMask() {
			window.ethereum.enable().then(this.provider = new ethers.providers.Web3Provider(window.ethereum));
			this.signer = this.provider.getSigner();
			this.functions = [];
		},
		readJson(file) {
			let reader = new FileReader()
			reader.onload = async (e) => {
				try {
					let document = JSON.parse(e.target.result)
					// console.log("document", document);
					this.contractAbi = document.abi.filter(func => func.type == "function");
					// console.log(this.contractAbi);
					const functions = this.contractAbi.map((func, index) => {
						return {
							value: index,
							label: func.name
						}
					});
					this.functions = functions;
				} catch (err) {
					console.log(`load JSON document from file error: ${err.message}`)
					this.showSnackbar(`Load JSON document from file error: ${err.message}`, 4000)
				}
			}
			reader.readAsText(file.raw)
		},
		change(value) {
			console.log(value);
			this.selectorIndex = value;
			const func = this.contractAbi[value];
			console.log(func);
			let params = "(";
			let paramsDesc = {};
			for(let i = 0; i < func.inputs.length; i++) {
				params += func.inputs[i].internalType + " " + func.inputs[i].name
				if(i != func.inputs.length - 1) {
					params += ",";
				}
				if(func.inputs[i].type == "tuple") {					
					paramsDesc[func.inputs[i].name] = {};
					for(let j = 0; j < func.inputs[i].components.length; j++) {
						const key = func.inputs[i].components[j].name;
						paramsDesc[func.inputs[i].name][key] = func.inputs[i].components[j].internalType;
					}
				}
			}
			params += ")";
			this.funcInfo = func.name + params + "\n"
			 + JSON.stringify(paramsDesc, null, "\t");
			this.encodeParams = ''
			this.encodeResult = ''
			this.decodeParams = ''
			this.decodeResult = ''
		},
		encode() {
			console.log(this.encodeParams);
			const params = this.encodeParams.split(";");
			const iface = new ethers.utils.Interface(this.contractAbi);
			let data;
			const func = this.contractAbi[this.selectorIndex];
			for(let i = 0; i < params.length; i++) {
				if(func.inputs[i].type == "tuple") {
					params[i] = JSON.parse(params[i]);
				}
			}
			if(func.inputs.length > 0) {
				data = iface.encodeFunctionData(func.name, params);
			} else {
				data = iface.encodeFunctionData(func.name);
			}
			
			this.encodeResult = data;
		},
		decode() {
			console.log(this.decodeParams);
			const iface = new ethers.utils.Interface(this.contractAbi);
			let data;
			const func = this.contractAbi[this.selectorIndex];
			if(func.inputs.length > 0) {
				data = iface.decodeFunctionData(func.name, this.decodeParams);
			} else {
				data = iface.decodeFunctionData(func.name);
			}
			console.log(data);
			console.log(data.toString());
			let newData = "";
			for(let i = 0; i < data.length; i++) {
				newData += data[i] + "\n";
			}
			this.decodeResult = newData;
		}
	}

}
</script>

<style>
body,
html {
	-webkit-user-select: text;
}

#workflow {
	display: flex;
}

.box {
	width: 650px;
	height: 650px;
	margin-right: auto;
}

.clearfix:before,
.clearfix:after {
	display: table;
	content: "";
}

.clearfix:after {
	clear: both
}

.outputs {
	word-break: break-all;
	word-wrap: break-word;
}
</style>
